<template>
  <div>
    <!-- Authentication form starts here -->
    <b-container>
      <b-row>
        <b-col sm="12">
          <b-form ref="confirm_authentication_ref" @submit.prevent.stop="confirmAuthentication">
            <b-row>
              <b-col>
                <!-- Hiding dummy text for now once got original text from product team will update  -->
                <!-- <p class="text-muted">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p> -->
              </b-col>
            </b-row>
            <b-row>
              <b-col md="12">
                <b-form-group
                  id="confirm_authentication"
                  :state="validation"
                  valid-feedback="Authentication key matched successfully!"
                  :invalid-feedback="invalidFeedback"
                  description="This document is protected by Sign.cc."
                >
                  <label>Authentication Key</label>
                  <b-form-input
                    id="confirm_authentication_key"
                    type="text"
                    maxLength="10"
                    v-model="form.authentication_key"
                    bg-variant="primary"
                    required
                    placeholder="Please enter the code. You should have received it in the email"
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>

            <hr />
            <b-row>
              <b-col class="text-right">
                <b-button variant="primary" size="sm" type="submit" :disabled="!validation">Verify</b-button>
              </b-col>
            </b-row>
          </b-form>
        </b-col>
      </b-row>
    </b-container>
    <!-- Authentication form ends here -->
  </div>
</template>

<script>
  export default {
    props: {
      sign_key: {
        type: String,
        required: true,
      },
    },
    data() {
      return {
        form: {
          authentication_key: "",
        },
        is_disabled: true,
      };
    },

    computed: {
      validation() {
        if (!this.form.authentication_key.length) return null;
        else return this.form.authentication_key.length == 10 && this.form.authentication_key == this.sign_key;
      },

      // Show Validation messages
      invalidFeedback() {
        let regex = /^\d+$/.test(this.form.authentication_key);
        if (!regex) {
          return "Key must be a number.";
        } else if (this.form.authentication_key.length) {
          return "Your key must be 10 characters long.";
        }
        return "";
      },
    },

    methods: {
      confirmAuthentication() {
        this.$emit("authenticationSuccess");
      },
    },
  };
</script>

<style lang="scss" scoped></style>
